#screen-game {
  display: none;
  flex-direction: column;
  align-items: center;
}

#chat {
  width: 650px;
  height: 158px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  font-size: 1.2em;
  border: 4px solid white;
  color: white;
  border-bottom: none;
}
#canvas {
  border: 4px solid white;
  border-bottom: none;
  cursor: none;
}
#chatInputBox {
  display: flex;
}
#chatInput {
  width: 575px;
  height: 32px;
  z-index: 1000;
  border: 4px solid white;
  background: black;
  color: white;
  font-family: "retro", sans-serif;
  outline: none;
}
#chatSendButton {
  width: 75px;
  height: 42px;
  border: 4px solid white;
  font-family: "retro", sans-serif;
  background: black;
  color: white;
  cursor: pointer;
  outline: none;
}
#ping {
  position: absolute;
  top: 5px; left: 128px;
  font-family: "retro", sans-serif;
  color: white;
}
#game-buttons {
  margin-top: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  button {
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 0.8em;
    font-family: "retro", sans-serif;
    border: 4px solid white;
    background: black;
    cursor: pointer;
  }
}

@media screen and (orientation:portrait) {
  #phone-vertical-warning {
    margin-top: 48px;
    display: flex;
    color: white;
  }

  #screen-game {
    div {
      display: none;
    }
    label {
      display: none;
    }
    canvas {
      display: none;
    }
  }
}

@media screen and (orientation:landscape) {
  #phone-vertical-warning {
    display: none;
  }
}
